export default {
  name: "SignUp",
  data() {
    return {
      user: {
        username: "",
        email: "",
        password: "",
      },
      // 错误
      errorVisible: false,
      // 正在注册
      signUpLoading: false,
      // 错误列表
      errorList: {},
    };
  },
  methods: {
    //
    handleSignUp() {
      this.signUpLoading = true;
      this.errorVisible = false;
      //
      this.$axios
        .post("/users", {
          user: this.user,
        })
        .then(({ data }) => {
          // 保存用户信息
          const { token } = data.user;
          localStorage.setItem("jwtToken", token);

          // 跳转至登录页
          this.$router.push("/");
        })
        .catch((error) => {
          const { errors } = error;
          this.errorList = errors;
          this.errorVisible = true;
        })
        .finally(() => {
          this.signUpLoading = false;
        });
    },
  },
};
